import { els } from './store/store.js';
import { addTodo } from './js/addTodo.js';
import { getTodos } from './js/getTodos.js';
import { renderTodos } from './js/renderTodos.js';

// Ï≤´ Î°úÎìúÏãú TO-DO Î†åÎçî
async function init() {
  els.loading.classList.add('show');  // Î°úÎî©
  let todos = await getTodos();
  renderTodos(todos);
  els.loading.classList.remove('show');  // Î°úÎî©
};
init();

// TO-DO Îì±Î°ù Í∏∞Îä•
els.todoAddBtn.addEventListener('click', async () => {
  const title = els.todoInput.value;
  if (title === '') {
    alert('Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!');
    return
  }
  els.loading.classList.add('show');  // Î°úÎî©

  // TO-DO Îì±Î°ù
  await addTodo(title);

  // Ï¥àÍ∏∞Ìôî
  els.todoList.innerHTML = "";
  els.todoInput.value = "";

  let todos = await getTodos();
  renderTodos(todos);

  els.loading.classList.remove('show');  // Î°úÎî©
});